.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 120px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; color:#fff;}
h1,h2,h3,h4,h5,h6 {font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; margin:.25rem 0 .5rem; color:#EFE05E; text-shadow:1px 1px 1px rgba(0,0,0,.5)}

p {
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin:0 0 3rem;
  line-height: 1.4;
}
h1 { font-size: 1.75rem;}
h2 { font-size: 1.375rem;}
h3 { font-size: 1.25rem;}
h4 { font-size: 1.125rem;}
h5 { font-size: 1.065rem;}
h6 { font-size: 1rem;}
code { border-radius: 2px; display:inline-block; vertical-align:middle; padding:.25rem .5rem; background:rgba(0,0,0,.3); border:1px solid #fff;}
p a {
  color:#eee;
  font-weight:bold;
}

.logo-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 1rem;
}
.logo-header img {
  width: 80px;
  height: 80px;
}
.logo-header .cross {
  font-size: 4rem;
  display:block;
  margin: 1rem;
  margin-right: 2rem;
}


